import Vue from 'vue'
import Router from 'vue-router'
import {httpi} from './http'
import Login from '@/pages/Login'
import error from '@/pages/Error'
import SystemCenter from '@/pages/SystemCenter'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [{
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/error',
      name: 'Error',
      component: error
    },
    {
      path: '/system-center',
      name: 'SystemCenter',
      component: SystemCenter
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Login') {
    httpi.get('/wyfx/function/verifyFunction', {
        params: {
          path: to.path
        }
      })
      .then(function (response) {
        if (response.data.code == '200') {
          next({
            name: 'SystemCenter'
          });
        } else if (response.data.code == '103') {
          next();
        } else {
          next({
            name: 'Error'
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        next({
          name: 'Error'
        });
      });
  } else if (to.name == 'Error') {
    next();
  } else {
    httpi.get('/wyfx/function/verifyFunction', {
        params: {
          path: to.path
        }
      })
      .then(function (response) {
        if (response.data.code == '200') {
          next();
        } else if (response.data.code == '103') {
          next({
            name: 'Login'
          });
        } else {
          next({
            name: 'Error'
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        next({
          name: 'Error'
        });
      });
  }
})

export default router



