<template>
  <div class="main" :style="main_bcg">
    <div class="container">
      <div class="card" :class="{hide:authority[1].ishide}" @click="handleCardClick('sf')">
        <i class="iconfont icon-device card-icon"></i>
        <div class="card-txt">{{language.sf}}</div>
      </div>
      <div class="card" :class="{hide:authority[2].ishide}" @click="handleCardClick('ft')">
        <i class="iconfont icon-file card-icon"></i>
        <div class="card-txt">{{language.fileTrack}}</div>
      </div>
      <div class="card" :class="{hide:authority[3].ishide}" @click="handleCardClick('am')">
        <i class="iconfont icon-file card-icon"></i>
        <div class="card-txt">{{language.anonymousAnalysis}}</div>
      </div>
      <div class="card" :class="{hide:authority[4].ishide}" @click="handleCardClick('EWM')">
        <i class="iconfont icon-file card-icon"></i>
        <div class="card-txt">{{language.EWM}}</div>
      </div>
    </div>
    <div class="container2">
      <div class="card" :class="{hide:authority[5].ishide}" @click="handleCardClick('Android')">
        <i class="iconfont icon-phone card-icon"></i>
        <div class="card-txt">{{language.Android}}</div>
      </div>
      <div class="card" :class="{hide:authority[6].ishide}" @click="handleCardClick('IOS')">
        <i class="iconfont icon-phone card-icon"></i>
        <div class="card-txt">{{language.IOS}}</div>
      </div>
      <div class="card" :class="{hide:authority[7].ishide}" @click="handleCardClick('Vikit')">
        <i class="iconfont icon-file card-icon"></i>
        <div class="card-txt">{{language.Vikit}}</div>
      </div>
    </div>
    <div class="header clearfix">
      <!-- <img :src="require('../assets/img/logo.png')" alt="" class="logo fl"> -->
      <div class="logo-txt fl">{{language.logoTxt}}</div>
      <div class="fr">
        <el-button class="right-set" :class="{hide:authority[0].ishide}">
          <i class="iconfont icon-system-config" style="margin-right:5px"></i>{{language.sysConfig}}
        </el-button>
        <el-dropdown trigger="click" menu-align="start" @command="handleLagChange">
          <el-button class="right-set">
            <i class="iconfont icon-language" style="margin-right:5px"></i>{{language.language}}
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="1">{{language.Ens}}</el-dropdown-item>
            <el-dropdown-item command="2">{{language.zhTw}}</el-dropdown-item>
            <el-dropdown-item command="3">{{language.zhCn}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown trigger="click" menu-align="start" @command="userCenter">
          <el-button class="right-set">
            <i class="iconfont icon-user" style="margin-right:5px"></i>{{userName}}
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="1">{{language.changePwd}}</el-dropdown-item>
            <el-dropdown-item command="2">{{language.signOut}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="footer">
      <p>{{language.footerInfo}}</p>
    </div>
    <el-dialog title="修改密碼" :visible.sync="updatePassVisible" size="tiny" :show-close="false" :close-on-click-modal="false">
      <el-form :model="updatePassForm" :rules="updatePassRules" ref="updatePassForm" label-width="100px">
        <el-form-item label="原密碼" prop="oldPwd">
          <el-input type="password" v-model="updatePassForm.oldPwd"></el-input>
        </el-form-item>
        <el-form-item label="新密碼" prop="newPwd">
          <el-input type="password" v-model="updatePassForm.newPwd"></el-input>
        </el-form-item>
        <el-form-item label="確認密碼" prop="surePwd">
          <el-input type="password" v-model="updatePassForm.surePwd"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="restPwdDialog('updatePassForm')">取 消</el-button>
        <el-button type="primary" @click="submitPwd('updatePassForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      const validateOldPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.language.cpwdNameNull));
        } else {
          callback();
        }
      };
      const validateNewPass = (rule, value, callback) => {
        const regExp = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,21}$/;
        if (value === '') {
          callback(new Error(this.language.cpwdPwdNull));
        } else if (!regExp.test(value)) {
          callback(new Error(this.language.cpwdPwdErr));
        } else {
          callback();
        }
      };
      const validateSurePass = (rule, value, callback) => {
        if (value === '' || value !== this.updatePassForm.newPwd) {
          callback(new Error(this.language.cpwdSurePwdErr));
        } else {
          callback();
        }
      };
      return {
        main_bcg: {
          backgroundImage: 'url(' + require('../assets/img/system-center-bcg.png') + ')',
        },
        authority: [{
            code: 'SystemConfig',
            ishide: true
          },
          {
            code: 'SF',
            ishide: true
          },
          {
            code: 'FileTrace',
            ishide: true
          },
          {
            code: 'AM',
            ishide: true
          },
          {
            code: 'EWM',
            ishide: true
          },
          {
            code: 'Android',
            ishide: true
          },
          {
            code: 'IOS',
            ishide: true
          },
          {
            code: 'Vikit',
            ishide: true
          }
        ],
        language: {
          logoTxt: '',
          sysConfig: '',
          language: '',
          Ens: '',
          zhTw: '',
          zhCn: '',
          changePwd: '',
          signOut: '',
          footerInfo: '',
          sf: '',
          mobTro: '',
          fileTrack: '',
          cpwdNameNull: '',
          cpwdPwdNull: '',
          cpwdPwdErr: '',
          cpwdSurePwdErr: ''
        },
        userName: '',
        updatePassVisible: false,
        updatePassForm: {
          oldPwd: '',
          newPwd: '',
          surePwd: ''
        },
        updatePassRules: {
          oldPwd: {
            validator: validateOldPass,
            trigger: 'blur'
          },
          newPwd: {
            validator: validateNewPass,
            trigger: 'blur'
          },
          surePwd: {
            validator: validateSurePass,
            trigger: 'blur'
          }
        },
      };
    },
    methods: {
      getArti(callBack) {
        const vm = this;
        //获取用户菜单权限
        this.$http.get('/wyfx/function/getMenuByLoginUser')
          .then(function(response) {
            let data = response.data.data;
            for (let i in data) {
              for (let j in vm.authority) {
                if (vm.authority[j].code == data[i].code) {
                  vm.authority[j].ishide = false;
                }
              }
            }
            callBack;
          })
      },
      getUser(callBack) {
        const vm = this;
        //获取用户信息
        this.$http.get('/wyfx/user/getLoginUser')
          .then(function(response) {
            if (response.data.code == '200') {
              vm.userName = response.data.msg;
            } else if (response.data.code == '103') {
              vm.$router.push({
                name: 'Login'
              });
            }
            callBack;
          })
      },
      getLang() {
        const vm = this;
        // 獲取文本信息
        this.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'SystemCenter'
            }
          })
          .then(function(response) {
            vm.language = response.data.data;
          })
      },
      handleLagChange(command) {
        if (command == "1") {
          this.languageChange('en_US');
        } else if (command == "2") {
          this.languageChange('zh_TW');
        } else if (command == "3") {
          this.languageChange('zh_CN');
        }
      },
      languageChange(val) {
        const vm = this;
        this.$http.get('/wyfx/page/changeLanguage', {
            params: {
              msg: val
            }
          })
          .then(function(response) {
            vm.$router.go(0);
          })
      },
      userCenter(command) {
        if (command == "1") {
          this.showPwdDialog();
        } else if (command == "2") {
          this.signOut();
        }
      },
      // 更改密码 显示对话框
      showPwdDialog() {
        this.updatePassVisible = true;
      },
      // 更改密码 提交表单
      submitPwd(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const vm = this;
            vm.$http.get('/wyfx/user/updatePass', {
                params: {
                  oldPass: vm.updatePassForm.oldPwd,
                  newPass: vm.updatePassForm.newPwd
                }
              })
              .then(function(response) {
                if (response.data.code == '200') {
                  vm.$message({
                    message: response.data.msg,
                    type: 'success'
                  });
                  vm.updatePassVisible = false;
                } else if (response.data.code == '101') {
                  vm.$message({
                    message: response.data.msg,
                    type: 'error'
                  });
                } else {
                  vm.$message({
                    message: response.data.msg,
                    type: 'error'
                  });
                }
              })
          } else {
            return false;
          }
        });
      },
      // 更改密码 关闭并重置对话框
      restPwdDialog(formName) {
        this.updatePassForm.oldPwd = '';
        this.updatePassForm.newPwd = '';
        this.updatePassForm.surePwd = '';
        this.$refs[formName].resetFields();
        this.updatePassVisible = false;
      },
      //登出
      signOut() {
        const vm = this;
        vm.$http.get('/wyfx/user/logout')
          .then(function(response) {
            vm.$router.push({
              name: 'Login'
            });
          })
      },
      // 跳轉到其他系統
      handleCardClick(url) {
        this.$http.get('/wyfx/page/redirect?path=' + url)
          .then(function(response) {
            window.location.href = response.data.msg;
          });
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'SystemCenter'
            }
          }),
          vm.$http.get('/wyfx/user/getLoginUser'),
          vm.$http.get('/wyfx/function/getMenuByLoginUser')
        ])
        .then(vm.$http.spread(function(language, user, menu) {
          // 獲取文本信息
          vm.language = language.data.data;
          // 用户信息
          if (user.data.code == '200') {
            vm.userName = user.data.msg;
          } else if (user.data.code == '103') {
            vm.$router.push({
              name: 'Login'
            });
          }
          // 菜单权限
          let data = menu.data.data;
            for (let i in data) {
              for (let j in vm.authority) {
                if (vm.authority[j].code == data[i].code) {
                  vm.authority[j].ishide = false;
                }
              }
            }
          loading.close();
        }))
        .catch(error => {
          loading.close();
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main {
    height: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    color: #fff;
  }
  .header {
    width: calc(100% - 80px);
    height: 50px;
    line-height: 50px;
    background-color: #292d30;
    padding: 0 40px;
    position: absolute;
    top: 0;
    left: 0;
  }
  .logo {
    height: 35px;
    margin-top: 7px;
  }
  .logo-txt {
    margin-left: 15px;
    font-size: 18px;
  }
  .right-set {
    background-color: #292d30;
    border: none;
    color: #fff;
  }
  .footer {
    width: 100%;
    height: 50px;
    align-items: center;
    display: -webkit-flex;
    color: #556e84;
    background-color: #292d30;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
  }
  .footer p {
    display: block;
    width: 1000px;
    margin: 0 auto;
    color: #fff;
  }
  .container {
    width: 960px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 100px;
    left: calc(50% - 480px);
  }
  .container2 {
    width: 960px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 300px;
    left: calc(50% - 480px);
  }
  .card {
    width: 200px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .5);
    color: #363d44;
    text-align: center;
    padding: 20px 0;
    cursor: pointer;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .1);
    margin: 20px;
    transition: all 0.5s;
  }
  .card:hover {
    transform: scale(1.15);
  }
  .card-icon {
    display: block;
    font-size: 60px;
  }
  .card-txt {
    margin-top: 10px;
    font-size: 20px;
  }
  .hide {
    display: none;
  }
</style>



