<template>
  <div class="main">
    <div class="error-imgbox">
      <img :src="require('../assets/img/404.png')" alt="" class="error-img">
    </div>
    <div class="error-txt">{{language.errTxt}}</div>
    <div class="error-btnbox">
      <el-button type="primary" @click="back">{{language.backBtn}}</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
      }
    },
    methods: {
      back() {
        this.$router.push({
          name: 'Login'
        });
      }
    },
    mounted() {
      const vm = this;
      this.$http.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'error'
          }
        })
        .then(function(response) {
          vm.language = response.data.data;
        })
    }
  }
</script>

<style scoped>
  .main {
    height: 100%;
  }
  .error-imgbox {
    width: 50%;
    margin: 0 auto;
  }
  .error-img {
    width: 100%;
  }
  .error-txt {
    color: #777f8f;
    font-size: 24px;
    text-align: center;
    margin-top: 80px;
  }
  .error-btnbox {
    text-align: center;
    margin-top: 30px;
  }
</style>



