<template>
  <div class="main">
    <div :style="backgroundDiv" class="backgroundDiv"></div>
    <el-dropdown trigger="click" menu-align="start" @command="handleLagChange" class="lageChan">
      <el-button class="right-set lageChan-btn">
        <i class="iconfont icon-language" style="margin-right:5px"></i>{{language.language}}
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="1">{{language.Ens}}</el-dropdown-item>
        <el-dropdown-item command="2">{{language.zhTw}}</el-dropdown-item>
        <el-dropdown-item command="3">{{language.zhCn}}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-box">
      <h3 class="login-title">{{language.loginTitle}}</h3>
      <el-form-item prop="name">
        <el-input type="text" v-model="ruleForm.name" :placeholder="language.userNamePls" @keyup.enter.native="submitForm('ruleForm')">
          <template slot="append">
            <i class="iconfont icon-username"></i>
          </template>
        </el-input>
      </el-form-item>
      <el-form-item prop="psw">
        <el-input type="password" v-model="ruleForm.psw" :placeholder="language.pwdPls" @keyup.enter.native="submitForm('ruleForm')">
          <template slot="append">
            <i class="iconfont icon-password"></i>
          </template>
        </el-input>
      </el-form-item>
      <el-form-item prop="verCode" class="clearfix">
        <el-input type="text" v-model="ruleForm.verCode" :placeholder="language.verCodePls" class="verCode fl" @keyup.enter.native="submitForm('ruleForm')"></el-input>
        <img id="codeValidateImg" @click="refreshVerCode" class="verImg fl">
      </el-form-item>
      <el-button type="primary" size="large" @click="submitForm('ruleForm')" class="subBtn" :loading="subLoading">{{language.subBtnTxt}}</el-button>
    </el-form>
    <div class="logo-box">
      <!-- <img :src="require('../assets/img/logo.png')" alt="" class="logo"> -->
      <div class="name">{{language.logoTxt}}</div>
    </div>
    <div class="footer">
      <p>{{language.footerInfo}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          logoTxt: '',
          loginTitle: '',
          userNamePls: '',
          userNameMes: '',
          pwdPls: '',
          pwdNameMes: '',
          verCodePls: '',
          verCodeMes: '',
          subBtnTxt: '',
          footerInfo: ''
        },
        subLoading: false,
        ruleForm: {
          name: '',
          psw: '',
          verCode: ''
        },
        rules: {
          name: {
            required: true,
            message: '',
            trigger: 'blur'
          },
          psw: {
            required: true,
            message: '',
            trigger: 'blur'
          },
          verCode: {
            required: true,
            message: '',
            trigger: 'blur'
          }
        },
        backgroundDiv: {
          backgroundImage: 'url(' + require('../assets/img/login-bcg.png') + ')',
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.subLoading = true;
            const vm = this;
            let data = new FormData();
            data.append('username', this.ruleForm.name);
            data.append('password', this.ruleForm.psw);
            data.append('code', this.ruleForm.verCode);
            this.$http.post('/wyfx/user/login', data)
              .then(function (response) {
                if (response.data.code == '200') {
                  vm.$router.push({
                    name: 'SystemCenter'
                  });
                } else if (response.data.code == '101') {
                  vm.$message({
                    message: response.data.msg,
                    type: 'error'
                  });
                } else if (response.data.code == '102') {
                  vm.$message({
                    message: response.data.msg,
                    type: 'error'
                  });
                } else if (response.data.code == '100') {
                  vm.$message({
                    message: response.data.msg,
                    type: 'error'
                  });
                }
                vm.subLoading = false;
              })
          } else {
            return false;
          }
        });
      },
      refreshVerCode() {
        var validateImagObject = document.getElementById("codeValidateImg");
        validateImagObject.src = this.$rootPath + '/wyfx/user/code?time=' + this.$getNowFormatDate();
      },
      handleLagChange(command) {
        if (command == "1") {
          this.languageChange('en_US');
        } else if (command == "2") {
          this.languageChange('zh_TW');
        } else if (command == "3") {
          this.languageChange('zh_CN');
        }
      },
      languageChange(val) {
        const vm = this;
        this.$http.get('/wyfx/page/changeLanguage', {
            params: {
              msg: val
            }
          })
          .then(function (response) {
            vm.$router.go(0);
          })
      },
      getLang(callBack) {
        const vm = this;
        // 獲取文本信息
        this.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Login'
            }
          })
          .then(function (response) {
            vm.language = response.data.data;
            vm.rules.name.message = vm.language.userNameMes;
            vm.rules.psw.message = vm.language.pwdNameMes;
            vm.rules.verCode.message = vm.language.verCodeMes;
            callBack;
          });
      }
    },
    mounted() {
      this.getLang(this.refreshVerCode());
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main {
    height: 100%;
    position: relative;
  }

  .backgroundDiv {
    height: 55%;
    background: -webkit-linear-gradient(#11d4c4, #049acd);
    background: -o-linear-gradient(#11d4c4, #049acd);
    background: -moz-linear-gradient(#11d4c4, #049acd);
    background: linear-gradient(#11d4c4, #049acd);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
  }

  .login-box {
    width: 350px;
    height: 260px;
    padding: 35px;
    position: absolute;
    top: 35%;
    left: calc(50% - 210px);
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px rgba(7, 117, 142, 0.1);
    border-radius: 10px;
  }

  .login-title {
    text-align: center;
    margin-bottom: 25px;
    font-weight: normal;
  }

  .subBtn {
    width: 100%;
  }

  .footer {
    width: 100%;
    height: 7%;
    align-items: center;
    display: -webkit-flex;
    color: #556e84;
    background-color: #e2e3e8;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
  }

  .name {
    color: #fff;
    font-size: 32px;
    margin-top: 10px;
  }

  .logo-box {
    position: absolute;
    width: 100%;
    left: 0;
    top: 5%;
    text-align: center;
  }

  .footer p {
    display: block;
    width: 500px;
    margin: 0 auto;
  }

  .verCode {
    width: 220px;
    margin-right: 20px;
  }

  .verImg {
    height: 35px;
    cursor: pointer;
  }

  .lageChan {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .lageChan-btn {
    background: transparent;
    border: none;
    color: #fff;
  }

</style>



